<template>
<div id="Login">
    <img id="LoginBanner" src="../assets/LoginBanner.webp" width="300px" height="300px">
    <form>
        <input class="CoProperty" type="text" placeholder="请输入账号" v-model="account">
        <input class="CoProperty" type="password" placeholder="请输入密码" v-model="password">
    </form>
    <button id="loginbutton" @click="login">登录</button>
    </div>
</template>
<script>
export default {
  name: 'Login',
  data () {
    return {
      account: null,
      password: null
    }
  },

  methods: {
    login: function () {
      var account = this.account
      var password = this.password
      this.axios({
        method: 'post',
        url: 'https://api.hzxnoheya.top:8889/api/verify',
        data: { account: account, password: password }
      })
        .then(res => {
          if (res.data.code === '1') { this.$router.push('/') } else { alert(res.data.msg) }
        }
        )
        .catch(error => (console.log(error)))
    }
  }
}

</script>
<style scoped>
#Login {
    margin: 20px auto;
    width: max-content;
}

#LoginBanner {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 20px auto;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.CoProperty {
    width: 315px;
    height: 35px;
    margin: 30px 0;
    display: block;
    border-radius: 4px;
    border: none;
    padding-left: 40px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    outline: none;
}

input[type=text] {
    background: url(../assets/account.webp) no-repeat left/35px 100%;
}

input[type=password] {
    background: url(../assets/password.webp) no-repeat left/35px 100%;
}

#loginbutton {
    display: block;
    background-color: #4c93f0;
    color: white;
    border: none;
    border-radius: 10px;
    margin: 30px auto;
    width: 35%;
    height: 40px;
}
</style>
